

const Card = () => {
  return (
    <label className="relative inline-flex items-center justify-center cursor-pointer
">

      <input type="checkbox" className="sr-only peer" />

      <div className="
    w-12 h-12
    rounded-full
    bg-blue-400
    flex items-center justify-center
    text-white text-xl
  ">
        ✕
      </div>

      <div className="
    absolute
    bottom-full
    mb-2
    hidden
    peer-checked:block
    z-10
  ">
        <div className="
      relative
      bg-white
      text-black
      text-sm
      px-4 py-2
      rounded-full
      shadow
      whitespace-nowrap
    ">
          Hello World

          <div className="
        absolute
        top-full left-1/2
        -translate-x-1/2
        w-0 h-0
        border-l-8 border-r-8 border-t-8
        border-l-transparent
        border-r-transparent
        border-t-white
      "></div>
        </div>
      </div>

    </label>

  );
}


export default Card;
