import GitHub from "@/components/icons/github";
import Link from "@/components/icons/link";
import View from "@/components/icons/polaroid";

export default class Links {

    private defaultDecorate = "bg-zinc-950/60 rounded-lg p-2 hover:scale-115 transition-all";

    public github(url: string) {

        return (
            <>
                <a
                    title="Ir al repositorio GitHub"
                    href={url}
                    target="_blank"
                    rel="noopener noreferrer"
                    className={this.defaultDecorate}
                >
                    <GitHub size={36} color="white" />
                </a>
            </>
        );
    }

    public website(url: string) {

        return (
            <>
                <a
                    title="Ir al sitio web"
                    href={url}
                    target="_blank"
                    rel="noopener noreferrer"
                    className={this.defaultDecorate}
                >
                    <Link size={36} color="white" />
                </a>
            </>
        );
    }

    public viewImages(url: string) {

        return (
            <>
                <a
                    title="Ver imágenes del proyecto"
                    href={url}
                    target="_blank"
                    rel="noopener noreferrer"
                    className={this.defaultDecorate}
                >
                    <View size={36} color="white" />
                </a>
            </>
        );
    }
}
